}
.toggle-btn {
    .button {
        position: relative; width: 50px; height: 25px; border-radius: 25px; background-color: #ddd; transition: all 0.2s; cursor: pointer;
        p{width: 72px; position: absolute; left: -80px; top:50%; transform: translateY(-50%); font-size: 14px; color: #777; text-align: right;}
    }
    .inside {
        position: absolute; top: 50%; left: 5px; width: 17px; height: 17px; border-radius: 50%; background-color: #fff; transform: translateY(-50%); transition: all 0.2s;
    }
    input:checked ~ label .button {
        background-color: $PRIMARY_COLOR;
        p{color: $PRIMARY_COLOR;}
    }
    input:checked ~ label .button .inside {
        left: 28px;
    }
}

