aside{
    > ul{
        >li{
            margin-bottom: 2rem;
            >a{ 
                font-size: 18px; font-weight: 500; margin-bottom: 1rem; display: inline-block;
            }
            > ul{
                margin-top: -5px;
                >li{
                    .item-new{ margin-left: 10px;}
                    &.active{
                        a{
                            &::before{
                                background-color: #0078ff; width: 100%;
                            }
                        }
                    }
                    a{
                        font-size: 14px; color: #888; line-height: 2.5; position: relative; transition: color .15s; display: inline-block; vertical-align: top;
                        &::before{
                            content: ''; width: 0; height: 2px; position: absolute; left: 0; bottom: 3px; transition: all .3s;
                        }
                    }
                    &.active{
                        a{
                            color: #0078ff; font-weight: 700;
                        }
                    }
                    ul{
                        padding-left: 20px; background-color: #f8f8f8;
                    }
                }
            }
        }
    }
}
.resume-login{
    width:80%; display: flex; flex-direction: column; justify-content: center; align-items: center; padding: 30px; border: 1px solid #ddd; margin-bottom: 40px;
    .btn{ display: block; margin-top: 10px; width: 100%;}
    .name{ font-size: 14px; margin: 10px 0;  
        b{font-size:20px;}
    }
}
.resume-login-profile{
    width: 80px; height: 80px; background-size: cover; 
}

@media screen and (max-width: 1024px) {
    aside{ display: none;}
}